@use '../util' as *;

.card {
  border-radius: rem(10);
  text-align: center;
  h1, h2, p {
    margin: 0;
    padding: 0;
  }
  max-width: rem(375);
  // height: 100%;
  
  // display: flex;
  // flex-direction: column;
  
  &_header {
    border-top-left-radius: rem(10);
    border-top-right-radius: rem(10);
    height: rem(160);
    // width: 100%;
    
    background: url(../../../images/bg-pattern-card.svg);
  }
  
  &_body {
    border-bottom-left-radius: rem(10);
    border-bottom-right-radius: rem(10);

    height: rem(430 - 160);

    background-color: white;

    display: flex;
    flex-direction: column;
    align-items: center;

    img {
      border-radius: 50%;
      margin-top: -50%;
      border: 5px solid white;
      margin-bottom: rem(20);
    }

    &--text {
      h2 {
        margin: 0;
        font-size: rem(15);
        font-weight: 400;
        color: var(--dark-gray);
        padding-top: rem(5);
      }
    }

    .name {
      display: flex;
      align-items: center;

      h1 {
        padding-right: rem(10);
        color: var(--dark-desat-blue);
      }

      p {
        color: var(--dark-gray);
      }
    }

    &--stats {
      margin-top: rem(40);
      width: 100%;
      display: flex;
      justify-content: space-around;
      border-top: rem(0.5) solid hsla(0, 0%, 59%, .4);
      
      h3 {
        text-transform: uppercase;
        color: var(--dark-desat-blue);
        margin-bottom: rem(10);
      }
      
      p {
        text-transform: capitalize;
        color: var(--dark-gray);
        font-size: rem(12);
      }
    }

  }
}