html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-main);
  background: var(--dark-bg);
  color: var(--dark-text-2);
  width: 100vw;
  height: 100vh;
  background: url(../../../images/bg-pattern-bottom.svg) top left;
  background-color: var(--dark-cyan);
  display: flex;
  justify-content: center;
  align-items: center;
}
:root {
  --dark-cyan: hsl(185, 75%, 39%);
  --dark-desat-blue: hsl(229, 23%, 23%);
  --dark-gray-blue: hsl(227, 10%, 46%);
  --dark-gray: hsl(0, 0%, 59%);
}

:root {
  --font-main: "Kumbh Sans", sans-serif;
}

.container {
  width: 375px;
  height: 430px;
  border-radius: 0.625rem;
}

.card {
  border-radius: 0.625rem;
  text-align: center;
  max-width: 23.4375rem;
}
.card h1, .card h2, .card p {
  margin: 0;
  padding: 0;
}
.card_header {
  border-top-left-radius: 0.625rem;
  border-top-right-radius: 0.625rem;
  height: 10rem;
  background: url(../../../images/bg-pattern-card.svg);
}
.card_body {
  border-bottom-left-radius: 0.625rem;
  border-bottom-right-radius: 0.625rem;
  height: 16.875rem;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.card_body img {
  border-radius: 50%;
  margin-top: -50%;
  border: 5px solid white;
  margin-bottom: 1.25rem;
}
.card_body--text h2 {
  margin: 0;
  font-size: 0.9375rem;
  font-weight: 400;
  color: var(--dark-gray);
  padding-top: 0.3125rem;
}
.card_body .name {
  display: flex;
  align-items: center;
}
.card_body .name h1 {
  padding-right: 0.625rem;
  color: var(--dark-desat-blue);
}
.card_body .name p {
  color: var(--dark-gray);
}
.card_body--stats {
  margin-top: 2.5rem;
  width: 100%;
  display: flex;
  justify-content: space-around;
  border-top: 0.03125rem solid rgba(150, 150, 150, 0.4);
}
.card_body--stats h3 {
  text-transform: uppercase;
  color: var(--dark-desat-blue);
  margin-bottom: 0.625rem;
}
.card_body--stats p {
  text-transform: capitalize;
  color: var(--dark-gray);
  font-size: 0.75rem;
}